@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Manrope";
  font-display: swap;
  src: url("../fonts/Manrope-Regular.woff2") format("woff2"), url("../fonts/Manrope-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 24px;
  font-family: "Manrope", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #323232;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.btn {
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  color: #ffffff;
  background-color: #244C98;
  border: none;
  border-radius: 60px;
  text-align: center;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--light {
  color: #244C98;
  background-color: #f0f4fc;
  border: 2px solid #244C98;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(50, 50, 50, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 80px 24px 14px;
  background-color: #f0f4fc;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 24px;
  right: 24px;
  z-index: 1;
  width: 52px;
  height: 52px;
  padding: 0;
  background: #ffffff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
@media (max-width: 767px) {
  .modal__close-btn {
    top: 10px;
    right: 22px;
    width: 40px;
    height: 40px;
  }
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 530px;
  padding: 14px 0 28px;
}
@media screen and (max-width: 767px) {
  .custom-input,
.custom-textarea {
    padding: 4px 0 48px;
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  max-width: 300px;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
@media screen and (max-width: 767px) {
  .custom-input__label,
.custom-textarea__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error,
.custom-textarea__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #15ab07;
}

.custom-toggle {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;
  display: flex;
  width: 500px;
  padding: 14px 0 10px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle {
    padding: 14px 0 10px 0;
    width: 100%;
  }
}
.custom-toggle:last-of-type {
  padding: 14px 0 58px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle:last-of-type {
    padding: 14px 0 58px 0;
  }
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-left: 52px;
  cursor: pointer;
}
.custom-toggle label span {
  margin-bottom: 0;
  margin-left: 0;
}
.custom-toggle--checkbox .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1.5px solid #2c39f2;
  transition: border 0.3s ease;
}
.custom-toggle--checkbox .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  background-color: rgba(44, 57, 242, 0);
  transform: translate(-50%, -50%);
  transition: background-color 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1px solid #2c39f2;
  border-radius: 50%;
  transition: border 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  color: rgba(255, 255, 255, 0);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: color 0.3s ease;
}
.custom-toggle input:checked + .custom-toggle__icon {
  border: 1.5px solid #2c39f2;
}
.custom-toggle input:checked + .custom-toggle__icon::before {
  background-color: #2c39f2;
}
.custom-toggle.is-invalid label span {
  color: #ff1553;
}
.custom-toggle .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-toggle .input-message.is-invalid {
  color: #ff0000;
}
.custom-toggle .input-message.is-valid {
  color: #15ab07;
}

.custom-select {
  position: relative;
  width: 300px;
  padding: 14px 0 28px;
  margin: 0 0 44px 0;
  /* &.is-valid {
    .custom-select__button {
      border: 2px solid $color-eucalyptus;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-eucalyptus, 0.5);
      }
    }
  } */
}
@media screen and (max-width: 767px) {
  .custom-select {
    padding: 4px 0 14px;
    width: 100%;
  }
}
.custom-select__label {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: transform 0.3s ease, opacity 0.3s ease;
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  .custom-select__label {
    transform: translateY(calc(-50% - 5px));
  }
}
.custom-select__placeholder {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: transform 0.3s ease, opacity 0.3s ease;
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  .custom-select__placeholder {
    transform: translateY(calc(-50% - 5px));
  }
}
.custom-select select {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.custom-select__button {
  display: flex;
  align-items: center;
  width: 100%;
  height: 48px;
  padding: 5px 10px;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  transition: border 0.3s ease;
}
.custom-select__button:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-select__button:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
.custom-select__button:disabled {
  pointer-events: none;
  opacity: 0.3;
}
.custom-select__list {
  position: absolute;
  bottom: 10px;
  z-index: 1;
  width: 100%;
  max-height: 100px;
  margin: 0;
  padding: 0;
  overflow-y: auto;
  list-style: none;
  background-color: #ffffff;
  border: 2px solid #000000;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease, opacity 0.3s ease;
}
.custom-select__item {
  padding: 5px;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.custom-select__item[aria-selected=true] {
  background-color: rgba(0, 0, 0, 0.2);
}
@media (hover: hover) {
  .custom-select__item[aria-selected=true]:hover:not(.focus-visible) {
    background-color: rgba(0, 0, 0, 0.2);
  }
}
.custom-select__item[aria-selected=true].focus-visible:focus {
  background-color: rgba(0, 0, 0, 0.2);
}
@media (hover: hover) {
  .custom-select__item:hover:not(.focus-visible) {
    background-color: rgba(0, 0, 0, 0.4);
  }
}
.custom-select__item.focus-visible:focus {
  background-color: rgba(0, 0, 0, 0.4);
}
.custom-select__error {
  position: absolute;
  bottom: -15px;
  left: 0;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.1s ease;
}
.custom-select__text {
  font-size: 20px;
  line-height: 24px;
  flex-shrink: 0;
  width: 80%;
  overflow: hidden;
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  opacity: 0;
  transition: opacity 0.3s ease 0.1s;
}
.custom-select__text br {
  display: none;
}
.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 10px;
  margin-left: auto;
  background-color: #000000;
  transition: transform 0.3s ease;
  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
}
.custom-select.is-disabled {
  pointer-events: none;
}
.custom-select.is-disabled .custom-select__list {
  display: none;
}
.custom-select.open-up .custom-select__list {
  bottom: auto;
  top: 0;
  transform: translateY(calc(-100% + 10px));
}
.custom-select.is-open .custom-select__list {
  visibility: visible;
  opacity: 1;
}
.custom-select.is-open .custom-select__icon {
  transform: rotate(180deg);
}
.custom-select.is-open .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__text {
  opacity: 1;
}
.custom-select.not-empty .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__placeholder {
  opacity: 0;
}
.custom-select.is-invalid .custom-select__error {
  opacity: 1;
}
.custom-select.is-invalid .custom-select__button {
  border: 2px solid #ff1553;
}
.custom-select.is-invalid .custom-select__button:focus, .custom-select.is-invalid .custom-select__button:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-select--placeholder .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select--placeholder.is-open .custom-select__label {
  opacity: 1;
}
.custom-select--placeholder.not-empty .custom-select__label {
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__label, .custom-select.slide-up.is-open .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__text, .custom-select.slide-up.is-open .custom-select__text {
  opacity: 1;
}
.custom-select .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-select .input-message.is-invalid {
  color: #ff0000;
}
.custom-select .input-message.is-valid {
  color: #15ab07;
}

