.form {
  display: flex;
  flex-direction: column;
}
.form p {
  margin: 0;
  margin-bottom: 18px;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  color: #323232;
  letter-spacing: -0.02em;
}
.form label {
  position: relative;
  font-size: 18px;
  line-height: 24px;
  color: #323232;
}
.form input {
  padding: 26px;
  margin-bottom: 8px;
  background-color: #d7e2f6;
  border: none;
  border-radius: 10px;
}
.form input::placeholder {
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  color: #1f4284;
}
.form__checkbox {
  margin-top: 18px;
  margin-bottom: 24px;
}
.form__mark {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  border: 2px solid #323232;
  border-radius: 4px;
}
.form input[type=checkbox]:checked + .form__mark::after {
  position: absolute;
  top: 4px;
  left: 4px;
  width: 9px;
  height: 6px;
  border-left: 2px solid #1f4284;
  border-bottom: 2px solid #1f4284;
  transform: rotate(-45deg);
  content: "";
}
.form__label {
  padding-left: 38px;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.02em;
}
.form__restore {
  margin-bottom: 36px;
  font-size: 20px;
  font-weight: 500;
  line-height: 27px;
  color: #244C98;
}
.form__button {
  padding: 18px;
  margin-bottom: 5px;
  border-radius: 4px;
}

